  openActivity(items){
    // this.router.navigate(['/menu/first/tabs/tab1/activity'],{ state: { activity: this.activity,theme:this.theme } });

    if(items.activity_action == 'iframe'){
      let opts : string = "location=yes,clearcache=yes,hidespinner=no"
      const browser = this._browser.create(items.iframe_url, '_blank', opts);
      var mimeType = '';
      browser.on('loadstart').subscribe(
        (data) => {
          

          // console.log("URL IS",data.url);
          var url = decodeURIComponent(data.url).replace(/\+/g, '%20').replace(/\+/g, '%2526').replace(/\+/g, '%2520');
          var urlSplits = url.split('/');
          var extension = url.substr(url.length - 4);
          if (extension == '.pdf') {
            this.shared.toastmsg('Downloading...')
            this.shared.showLoading();
            mimeType = "application/pdf"
            var targetPath = this.file.externalRootDirectory + "Download/" + urlSplits[urlSplits.length - 1];
            var options = {};
            var args = {
              url: url,
              targetPath: targetPath,
              options: options,
              mimeType: mimeType
            };
            browser.close(); // close window or you get exception
            var self = this;
            document.addEventListener('deviceready', function () {
              setTimeout(function() {
                self.downloadfile(args); // call the function which will download the file 1s after the window is closed, just in case..
              }, 1000);
            });
          }else if(extension == '.gpx'){
            this.shared.toastmsg('Downloading...')
            this.shared.showLoading();
            mimeType = "application/octet-stream"
            var targetPath = this.file.externalRootDirectory + "Download/" + urlSplits[urlSplits.length - 1];
            var options = {};
            // alert(targetPath)
            var args = {
              url: url,
              targetPath: targetPath,
              options: options,
              mimeType: mimeType
            };
            browser.close(); // close window or you get exception
            var self = this;
            document.addEventListener('deviceready', function () {
              setTimeout(function() {
                self.downloadfile(args); // call the function which will download the file 1s after the window is closed, just in case..
              }, 1000);
            });
          }
          // this.downloadfile(data)
        },
        err => {
          console.log("InAppBrowser Loadstop Event Error: " + err);
        })
    }else{
      this.router.navigate(['/menu/first/tabs/tab1/activity-next'], { state: { data: items } });
    }



  }

  downloadfile(args)
  {
    const fileTransfer: FileTransferObject = this.transfer.create();
    var uri = encodeURI(args.url);

    fileTransfer.download(
      uri, // file's uri
      args.targetPath,).then((entry) => {
      console.log('download complete: ' + entry.toURL());
      this.shared.hideLoading();
      this.shared.openFile(entry.toURL(),args.mimeType);
      // if(args.mimeType == 'application/pdf'){
        
      // }else{
      //   this.file.readAsText()
      // }
    }, (error) => {
      console.log(error)
      this.shared.hideLoading();
      // handle error
    });
  }



